import React, { forwardRef } from "react";
import { avatarSize, AvatarProps, defaultAvatarProps } from "./Avatar.types";
import { AvatarContainer, AvatarImage, AvatarUsername } from "./Avatar.styled";
import { getUserInital } from "src/utils";

export const Avatar = forwardRef<HTMLImageElement, AvatarProps>(
  (
    {
      id,
      alt,
      src,
      size,
      radius,
      elevation,
      fit,
      className,
      imageClassName,
      fallbackClassName,
      onClick,
      onLoadingStatusChange,
    },
    ref
  ) => {
    const fallbackFontSize = avatarSize[size ?? defaultAvatarProps.size!] * 0.6;
    return (
      <AvatarContainer
        className={className}
        css={{
          borderRadius: `$${radius}`,
          boxShadow: `$${elevation}`,
        }}
        size={size}
        onClick={onClick}
      >
        <AvatarImage
          id={id}
          ref={ref}
          src={src}
          alt={alt}
          css={{
            objectFit: fit,
          }}
          className={imageClassName}
          onLoadingStatusChange={onLoadingStatusChange}
        />
        <AvatarUsername
          css={{
            fontSize: fallbackFontSize,
          }}
          className={fallbackClassName}
        >
          {getUserInital(alt)}
        </AvatarUsername>
      </AvatarContainer>
    );
  }
);

const componentName = Avatar.displayName || Avatar.name;
Avatar.defaultProps = defaultAvatarProps;
Avatar.displayName = "Avatar" + "(" + componentName + ")";
